@media screen and (min-width: 1000px) {
  .lang__type:not(.active):hover {
    background-color: #9F9889;
  }
  .header__nav-link:hover {
    color: #C56C1A;
  }
  .button_orange:hover {
    background-color: transparent;
    color: #C56C1A;
    border: 1px solid #C56C1A;
  }
}
* {
  box-sizing: border-box;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-family: "Bitter", serif;
  font-style: normal;
  background-color: #EAE3D3;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1394px;
  padding: 0 25px;
  margin: 0 auto;
}

.lang {
  position: absolute;
  top: 0;
  left: 25px;
  display: flex;
}
.lang__type {
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 32px;
  background-color: #746B60;
  color: #fff;
  transition: 0.25s;
}
.lang__type.active {
  background-color: #fff;
  color: #000;
}
.lang__ru.active::before {
  position: absolute;
  content: "";
  top: 100%;
  left: 0;
  clip-path: polygon(0 0, 0% 100%, 100% 0);
  background-color: #fff;
  width: 9.29px;
  height: 8.61px;
}
.lang__eng.active::before {
  position: absolute;
  content: "";
  top: 100%;
  right: 0;
  clip-path: polygon(0 0, 100% 100%, 100% 0);
  background-color: #fff;
  width: 9.29px;
  height: 8.61px;
}

.social {
  display: flex;
  align-items: center;
}
.social-icon {
  display: block;
  margin-right: 43px;
}
.social-icon:last-of-type {
  margin-right: 0;
}

.button {
  display: flex;
  align-items: center;
  width: fit-content;
  transition: all 0.25s;
}
.button_light {
  background-color: #F7F3EA;
  color: #C56C1A;
  font-weight: 500;
  font-size: 16px;
  line-height: 180%;
  padding: 11.5px 40px;
  text-transform: uppercase;
  z-index: 2;
  position: relative;
  margin-left: 52px;
}
.button_orange {
  background-color: #C56C1A;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 16px;
  line-height: 90%;
  padding: 17px 42px;
  border: 1px solid #C56C1A;
  text-transform: uppercase;
}
.button_orange:active {
  background-color: transparent;
  color: #C56C1A;
  border: 2px solid #C56C1A;
  padding: 16px 41px;
}

.mobile-nav {
  display: none;
}

.header {
  position: relative;
}
.header__grid {
  display: grid;
  grid-template-columns: 274px auto;
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 116px;
  padding: 95px 0;
}
.header__menu {
  display: none;
}
.header .logo {
  padding: 10px 0 8px;
  width: 274px;
  grid-row: span 2;
}
.header .logo img {
  display: block;
  max-width: 100%;
  transform: translate(1px, 3px);
}
.header__top {
  align-self: start;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__dots {
  flex: 1;
  height: 1px;
  margin: 0 44px;
  background-image: url(../img/dots.svg);
  background-position: center center;
}
.header .visually-impaired {
  display: flex;
  align-items: center;
}
.header .visually-impaired img {
  display: block;
  margin-right: 13px;
}
.header .visually-impaired span {
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  text-transform: uppercase;
  color: #2A2926;
}
.header .visually-impaired-mobile {
  display: none;
}
.header__nav {
  align-self: end;
  display: flex;
  align-items: center;
}
.header__nav-link {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  text-transform: uppercase;
  color: #2A2926;
  margin-right: 71px;
  transition: 0.25s;
}
.header__nav-link:nth-of-type(5) {
  margin-right: 0;
}
.header__nav-link:last-of-type {
  margin-left: auto;
  margin-right: 0;
}
.header__nav-link.active {
  color: #C56C1A;
  text-decoration: underline;
}
.header__nav-link.dropdown {
  margin-right: 92px;
}
.header__nav-link.dropdown::after {
  content: url(../img/dropdown.svg);
  position: absolute;
  left: calc(100% + 12px);
  top: 50%;
  transform: translateY(-50%);
}
.header__decor {
  position: absolute;
  left: 25px;
  top: 100%;
  display: flex;
  align-items: center;
}
.header__decor-item {
  width: 30px;
  height: 8px;
  background-color: #EAE3D3;
  margin-right: 26px;
  z-index: 2;
}
.header__decor-item:nth-of-type(1) {
  position: relative;
  background-color: #fff;
}
.header__decor-item:nth-of-type(1)::before {
  position: absolute;
  content: "";
  left: 0;
  top: calc(100% - 1px);
  width: 8px;
  height: 8px;
  background-color: #fff;
  clip-path: polygon(0 0, 0% 100%, 100% 0);
}

.slider .swiper {
  overflow: visible;
}
.slider .swiper-slide {
  padding-bottom: 32.1%;
}
.slider__slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.slider__slide::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(200.18deg, rgba(0, 0, 0, 0.6) 21.94%, rgba(0, 0, 0, 0.34375) 51.9%, rgba(0, 0, 0, 0) 88.45%);
  opacity: 0.4;
  transform: matrix(1, 0, 0, -1, 0, 0);
}
.slider__slide .container {
  position: relative;
  height: 100%;
  z-index: 1;
}
.slider__slide_1 {
  background-image: url(../img/slide-1.jpg);
}
.slider__slide_2 {
  background-image: url(../img/slide-2.jpg);
}
.slider__slide_3 {
  background-image: url(../img/slide-3.jpg);
}
.slider__slide_4 {
  background-image: url(../img/slide-4.jpg);
}
.slider__slide-wrap {
  position: relative;
  display: flex;
  align-items: flex-end;
  padding-bottom: 103px;
  height: 100%;
}
.slider__slide-title {
  font-weight: 600;
  font-size: 80px;
  line-height: 100%;
  letter-spacing: -0.03em;
  color: #FFFFFF;
  max-width: 75%;
  opacity: 1;
  margin: 0;
}
.slider__slide-info {
  position: absolute;
  left: 0;
  bottom: -214px;
  width: 548px;
  padding: 50px;
  background-color: #ffffff;
}
.slider__slide-info::before {
  position: absolute;
  content: "";
  left: 0;
  bottom: calc(100% - 1px);
  width: 24px;
  height: 24px;
  clip-path: polygon(0 0, 0% 100%, 100% 100%);
  background-color: #fff;
}
.slider__slide-info-text {
  font-weight: 300;
  font-size: 16px;
  line-height: 180%;
  color: #000000;
  transition: all 0.5s;
}
.slider__slide-info-text a {
  color: #C56C1A;
  text-decoration: underline;
}
.slider__slide-info-text.closing {
  transform: translateY(-70px);
  opacity: 0;
}
.slider__bottom-row {
  display: flex;
  align-items: flex-end;
  padding: 88px 0 90px;
}
.slider__bottom-part {
  width: 50%;
}
.slider__bottom-text {
  font-weight: 300;
  font-size: 20px;
  line-height: 180%;
  color: #000000;
  margin-bottom: 35px;
}
.slider-prev {
  position: absolute;
  bottom: 122px;
  left: calc((100vw - 1394px) / 2 - 67px);
  width: 32px;
  height: 32px;
  clip-path: polygon(15% 50%, 100% 100%, 100% 0);
  background-color: #fff;
  z-index: 1;
  cursor: pointer;
}
.slider-next {
  position: absolute;
  bottom: 122px;
  right: calc((100vw - 1394px) / 2 - 67px);
  width: 32px;
  height: 32px;
  clip-path: polygon(0 0, 0% 100%, 85% 50%);
  background-color: #fff;
  z-index: 1;
  cursor: pointer;
}

@media screen and (max-width: 1660px) {
  .slider__slide-title {
    padding-left: 30px;
  }
  .slider-prev {
    left: calc((100vw - 1394px) / 2 - 1px);
  }
  .slider-next {
    right: calc((100vw - 1394px) / 2 - 1px);
  }
}
@media screen and (max-width: 1440px) {
  .slider__slide-title {
    padding-left: 30px;
  }
  .slider-prev {
    width: 24px;
    height: 24px;
  }
  .slider-next {
    width: 24px;
    height: 24px;
  }
}
@media screen and (max-width: 1400px) {
  .container {
    max-width: 1200px;
    padding: 0 24px;
  }
  .header__grid {
    grid-template-columns: 240px auto;
    grid-column-gap: 80px;
  }
  .header .logo {
    width: 240px;
  }
  .header__nav {
    justify-content: space-between;
  }
  .header__nav-link {
    margin-right: 0;
  }
  .header__nav-link.dropdown {
    margin-right: 0;
  }
  .header__nav-link:last-of-type {
    margin-left: 0;
  }
  .slider__slide-title {
    font-size: 60px;
    max-width: 68%;
  }
  .slider__slide-info {
    width: calc((100vw - 48px) / 2);
    max-width: 548px;
  }
  .slider__bottom-text {
    font-size: 16px;
  }
  .slider-prev {
    bottom: 110px;
    left: calc((100vw - 1200px) / 2 - 1px);
  }
  .slider-next {
    bottom: 110px;
    right: calc((100vw - 1200px) / 2 + 1px);
  }
}
@media screen and (max-width: 1200px) {
  .header__grid {
    padding: 60px 0;
    grid-column-gap: 60px;
  }
  .header .logo {
    width: 234px;
  }
  .header__nav-link {
    font-size: 14px;
  }
  .header__nav-link.dropdown::after {
    left: calc(100% + 4px);
  }
  .slider .swiper-slide {
    padding-bottom: 45%;
  }
  .slider__slide-wrap {
    padding-bottom: 98px;
  }
  .slider__slide-info {
    width: calc(100vw - 24px);
  }
  .slider-prev {
    height: 18px;
    width: 18px;
    left: 6px;
  }
  .slider-next {
    height: 18px;
    width: 18px;
    right: 6px;
  }
  .slider__bottom-row {
    display: block;
    padding: 214px 0 60px;
  }
  .slider__bottom-part {
    width: 100%;
  }
  .slider__bottom-part:last-of-type {
    margin-top: 64px;
  }
  .button_light {
    margin-left: 0;
  }
}
@media screen and (max-width: 992px) {
  .lang {
    left: auto;
    right: 25px;
  }
  .social {
    flex-direction: column;
  }
  .social-icon {
    margin-right: 0;
    margin-top: 27px;
  }
  .social-icon:nth-of-type(2) {
    order: -1;
  }
  .mobile-nav {
    position: absolute;
    top: 0;
    left: 0;
    width: 270px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 65px 43px;
    background-color: #F6F3EA;
    z-index: 5;
    transition: all 0.25s;
    transform: translateX(-270px);
  }
  .mobile-nav::after {
    display: none;
  }
  .mobile-nav.opened::after {
    display: block;
    position: absolute;
    content: "";
    top: 37px;
    left: 100%;
    width: 10px;
    height: 12px;
    background-color: #F6F3EA;
    clip-path: polygon(0 0, 0% 100%, 100% 50%);
  }
  .mobile-nav__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    text-transform: uppercase;
    color: #2A2926;
    margin-bottom: 30px;
  }
  .mobile-nav__link.active {
    color: #C56C1A;
    text-decoration: underline;
  }
  .header {
    transition: all 0.25s;
  }
  .header.opened {
    transform: translateX(270px);
  }
  .header__grid {
    grid-template-rows: auto auto auto;
    grid-template-columns: auto 38px;
    grid-column-gap: 40px;
    grid-row-gap: 46px;
    padding: 32px 0 42px;
  }
  .header__menu {
    display: flex;
    align-items: center;
    padding-left: 8px;
  }
  .header__menu-item {
    margin-right: 24px;
    cursor: pointer;
  }
  .header__menu-item:last-of-type {
    margin-right: 0;
  }
  .header .logo {
    grid-row: span 1;
    order: 1;
    padding: 0;
  }
  .header__top {
    grid-row: span 3;
    align-self: flex-end;
  }
  .header__dots {
    display: none;
  }
  .header .visually-impaired {
    display: none;
  }
  .header .visually-impaired-mobile {
    display: flex;
    align-items: center;
    order: 1;
    padding-top: 4px;
  }
  .header .visually-impaired-mobile img {
    display: block;
    margin-right: 12px;
  }
  .header .visually-impaired-mobile span {
    font-weight: 600;
    font-size: 16px;
    line-height: 146%;
    text-transform: uppercase;
    color: #746B60;
  }
  .header__nav {
    display: none;
  }
  .header__decor {
    left: 46px;
  }
  .header__decor-item {
    width: 24px;
    height: 6.4px;
    margin-right: 12.8px;
  }
  .header__decor-item:nth-of-type(1)::before {
    width: 6.4px;
    height: 6.4px;
  }
  .slider {
    transition: all 0.25s;
  }
  .slider.opened {
    transform: translateX(270px);
  }
  .slider__slide-title {
    max-width: 80%;
  }
}
@media screen and (max-width: 768px) {
  .slider .swiper-slide {
    padding-bottom: 60%;
  }
  .slider__slide {
    background-position: center center;
  }
  .slider__slide-wrap {
    padding-bottom: 74px;
  }
  .slider__slide-title {
    font-size: 46px;
  }
  .slider-prev, .slider-next {
    bottom: 82px;
  }
}
@media screen and (max-width: 576px) {
  .lang {
    right: 0;
  }
  .lang__type {
    width: 78px;
    height: 42px;
  }
  .slider .swiper-slide {
    padding-bottom: 105%;
  }
  .slider__slide-wrap {
    padding-bottom: 82px;
  }
  .slider__slide-title {
    font-size: 36px;
    line-height: 90%;
    max-width: 85%;
    padding-left: 24px;
  }
  .slider__slide-info {
    padding: 30px;
    bottom: -174px;
  }
  .slider__slide-info-text {
    font-size: 14px;
  }
  .slider__bottom-row {
    padding: 174px 0 60px;
  }
  .slider__bottom-text {
    margin-bottom: 26px;
  }
  .slider-prev, .slider-next {
    bottom: 86px;
  }
  .button_orange {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}